	module.exports = {"data":{"markdownRemark":{"html":"<h3>Custom Fields</h3>\n<p>After the beta launch in March, <a href=\"/programs/custom-fields.html\">custom fields</a> is now available to all Enterprise programs.  </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/mar_2019_custom_fields-51fbd39f3ab0120007c2b4b43d2e322d-0c604.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 33.093525179856115%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"custom fields\"\n        title=\"\"\n        src=\"/static/mar_2019_custom_fields-51fbd39f3ab0120007c2b4b43d2e322d-2f183.png\"\n        srcset=\"/static/mar_2019_custom_fields-51fbd39f3ab0120007c2b4b43d2e322d-67829.png 125w,\n/static/mar_2019_custom_fields-51fbd39f3ab0120007c2b4b43d2e322d-5c59f.png 250w,\n/static/mar_2019_custom_fields-51fbd39f3ab0120007c2b4b43d2e322d-2f183.png 500w,\n/static/mar_2019_custom_fields-51fbd39f3ab0120007c2b4b43d2e322d-c93e8.png 750w,\n/static/mar_2019_custom_fields-51fbd39f3ab0120007c2b4b43d2e322d-4e628.png 1000w,\n/static/mar_2019_custom_fields-51fbd39f3ab0120007c2b4b43d2e322d-0c604.png 1390w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Mark a report as ineligible for bounty when awarding swag</h3>\n<p>We introduce the new <strong>Also mark as ineligible for bounty</strong> checkbox so that programs can now mark a report as being ineligible for bounty when awarding swag.</p>\n<p>Programs are also able to now award swag to reports that are marked as ineligible for bounty.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/june_2019_mark_ineligible-c355abc7d6cfa35df7997e9f2e1f274c-7b85b.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 32.778489116517285%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"mark as ineligible for bounty checkbox\"\n        title=\"\"\n        src=\"/static/june_2019_mark_ineligible-c355abc7d6cfa35df7997e9f2e1f274c-2f183.png\"\n        srcset=\"/static/june_2019_mark_ineligible-c355abc7d6cfa35df7997e9f2e1f274c-67829.png 125w,\n/static/june_2019_mark_ineligible-c355abc7d6cfa35df7997e9f2e1f274c-5c59f.png 250w,\n/static/june_2019_mark_ineligible-c355abc7d6cfa35df7997e9f2e1f274c-2f183.png 500w,\n/static/june_2019_mark_ineligible-c355abc7d6cfa35df7997e9f2e1f274c-c93e8.png 750w,\n/static/june_2019_mark_ineligible-c355abc7d6cfa35df7997e9f2e1f274c-4e628.png 1000w,\n/static/june_2019_mark_ineligible-c355abc7d6cfa35df7997e9f2e1f274c-b3ac4.png 1500w,\n/static/june_2019_mark_ineligible-c355abc7d6cfa35df7997e9f2e1f274c-7b85b.png 1562w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Notifications Bell Icon</h3>\n<p>Say goodbye to the old notifications indicator next to your profile icon. We introduce a new bell icon to notify you of any new notifications. If you have more than 25 unread notifications, we've truncated the notifications number to be capped at <strong><em>25+</em></strong>.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/june_2019_bell_icon-9e26b8b51a299a90c96dc18712550e27-7d7b6.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 242px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 36.36363636363637%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"bell icon\"\n        title=\"\"\n        src=\"/static/june_2019_bell_icon-9e26b8b51a299a90c96dc18712550e27-7d7b6.png\"\n        srcset=\"/static/june_2019_bell_icon-9e26b8b51a299a90c96dc18712550e27-b2b34.png 125w,\n/static/june_2019_bell_icon-9e26b8b51a299a90c96dc18712550e27-7d7b6.png 242w\"\n        sizes=\"(max-width: 242px) 100vw, 242px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Saving the Inbox Sort Order</h3>\n<p>Say goodbye to having to always re-select how you want your inbox sorted. Your selected sort order will now automatically save when creating your custom inbox view.  </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/june_2019_save_sort_order-f915272e824dc94e1e1c586eb4a11caa-7b47c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 74.6875%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"save sort order\"\n        title=\"\"\n        src=\"/static/june_2019_save_sort_order-f915272e824dc94e1e1c586eb4a11caa-2f183.png\"\n        srcset=\"/static/june_2019_save_sort_order-f915272e824dc94e1e1c586eb4a11caa-67829.png 125w,\n/static/june_2019_save_sort_order-f915272e824dc94e1e1c586eb4a11caa-5c59f.png 250w,\n/static/june_2019_save_sort_order-f915272e824dc94e1e1c586eb4a11caa-2f183.png 500w,\n/static/june_2019_save_sort_order-f915272e824dc94e1e1c586eb4a11caa-7b47c.png 640w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Submission Date Filters</h3>\n<p>You can now filter through your inbox reports by submission date in order to quickly find reports submitted after or within a certain time.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/june_2019_date_filters-e4d89d353870aabaa3d1e033fef3d5c1-f54cd.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 62.572254335260126%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"submission date filters\"\n        title=\"\"\n        src=\"/static/june_2019_date_filters-e4d89d353870aabaa3d1e033fef3d5c1-2f183.png\"\n        srcset=\"/static/june_2019_date_filters-e4d89d353870aabaa3d1e033fef3d5c1-67829.png 125w,\n/static/june_2019_date_filters-e4d89d353870aabaa3d1e033fef3d5c1-5c59f.png 250w,\n/static/june_2019_date_filters-e4d89d353870aabaa3d1e033fef3d5c1-2f183.png 500w,\n/static/june_2019_date_filters-e4d89d353870aabaa3d1e033fef3d5c1-c93e8.png 750w,\n/static/june_2019_date_filters-e4d89d353870aabaa3d1e033fef3d5c1-4e628.png 1000w,\n/static/june_2019_date_filters-e4d89d353870aabaa3d1e033fef3d5c1-f54cd.png 1384w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Goodbye to the Insights Tab</h3>\n<p>We’ve deprecated the Insights tab from program pages.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/june_2019_insights-dc1e772ee218c7fe4ecff4f70bfd6cbf-6a078.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 41.219008264462815%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"insights tab\"\n        title=\"\"\n        src=\"/static/june_2019_insights-dc1e772ee218c7fe4ecff4f70bfd6cbf-2f183.png\"\n        srcset=\"/static/june_2019_insights-dc1e772ee218c7fe4ecff4f70bfd6cbf-67829.png 125w,\n/static/june_2019_insights-dc1e772ee218c7fe4ecff4f70bfd6cbf-5c59f.png 250w,\n/static/june_2019_insights-dc1e772ee218c7fe4ecff4f70bfd6cbf-2f183.png 500w,\n/static/june_2019_insights-dc1e772ee218c7fe4ecff4f70bfd6cbf-c93e8.png 750w,\n/static/june_2019_insights-dc1e772ee218c7fe4ecff4f70bfd6cbf-4e628.png 1000w,\n/static/june_2019_insights-dc1e772ee218c7fe4ecff4f70bfd6cbf-b3ac4.png 1500w,\n/static/june_2019_insights-dc1e772ee218c7fe4ecff4f70bfd6cbf-6a078.png 1936w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/changelog/2019/june","title":"June 2019"}}},"pathContext":{}}

